import sound from '../sounds/sound.mp3';

import makeSample from './sound';
import Engine from './utils/engine';
import { trackMouse } from './utils/utils';

const WIDTH = 480;
const HEIGHT = 480;

const el = document.getElementById('canvas');
const context = new AudioContext();

let waveform = false;
const waveformSections = 50;

let audioBuffer = false;
let audioSource = false;

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

function getSoundAsBuffer(src) {
  return fetch(src)
    .then(resp => resp.arrayBuffer());
}

function chopToFloats(buffer, segments = 10) {
  const left = buffer.getChannelData(0);
  const right = buffer.getChannelData(1);

  const section = Math.floor(left.length / segments);

  const data = [];

  for (let i = 0; i < segments; i++) {
    let idx = section * i;
    let float = (((left[idx] + right[idx]) / 2) + 1) / 2;
    data.push(float);
  }

  return Promise.resolve(data);
}

function analyse() {
  // First fetch the sound
  return getSoundAsBuffer(sound)
    // Then create an actual audio buffer from that data
    .then(buffer => context.decodeAudioData(buffer))
    // Cut it into the relevant stuff for our visualisation
    .then(buffer => {
      audioBuffer = buffer;
      return chopToFloats(buffer, waveformSections)
    })
    // Save the data for our waveform
    .then(data => {
      waveform = data
      return data;
    });
}




function drawWaveform(wave, width, height) {
  let pad = 1;
  let wid = width / waveformSections;
  for (let i = 0; i < waveform.length; i++) {
    let y = ((1 - waveform[i]) * height) / 2;
    ctx.fillRect((i * wid) + pad, y, wid - (pad * 2), waveform[i] * height);
  }
}

let songPercentage = 0;
let sounds = false;
let soundColours = ['red', 'green', 'blue', 'yellow'];
let sampleDuration = 500;
let sampleTarget = 0;
let currSample = 0;

function onTimeUpdate(time, perc) {
  songPercentage = perc;
}

function drawShape(idx, x, y, size) {
  ctx.save();

  ctx.beginPath();
  ctx.arc(x, y, size / 2, 0, Math.PI * 2);

  ctx.fillStyle = soundColours[idx];
  ctx.fill();

  ctx.restore();
}

app.onRender(({ frame, width, height, debug }) => {
  ctx.clearRect(0, 0, width, height);
  if (waveform) {

    if (sounds) {
      for (let i = 0; i < sounds.length; i++) {
        ctx.save();
        ctx.translate(sounds[i].getStart().startPerc * width, 0);
        ctx.fillStyle = soundColours[i];
        ctx.fillRect(0, 0, ((sampleDuration / 1000) / sounds[i].getDuration()) * width, height);
        ctx.restore();
      }
    }

    drawWaveform(waveform, width, height);


    drawShape(sampleTarget, width - 50, height - 50, height * 0.1);

    ctx.save();
    ctx.strokeStyle = 'red';
    ctx.translate(width * songPercentage, 0)
    ctx.beginPath();
    ctx.moveTo(0, 0);
    ctx.lineTo(0, height);
    ctx.stroke();
    ctx.restore();


  }
});

app.start();
analyse()
  .then(() => {
    sounds = [
      makeSample(audioBuffer, context, 10),
      makeSample(audioBuffer, context, 20),
      makeSample(audioBuffer, context, 30),
      makeSample(audioBuffer, context, 40),
    ];
    sounds.forEach(s => s.onTime(onTimeUpdate));

    const playNote = idx => {
      for (let i = 0; i < sounds.length; i++) {
        if (i == idx) {
          sounds[i].play(sampleDuration / 1000);
          currSample = i;
        } else {
          sounds[i].stop();
        }
      }

      idx++;
      setTimeout(() => playNote(idx % sounds.length), sampleDuration);
    };
    playNote(0);

    app.start();
    trackMouse();
  });

  document.body.addEventListener('keyup', e => {
    switch (e.key) {
      case '1':
        sampleTarget = 0;
        break;
      case '2':
        sampleTarget = 1;
        break;
      case '3':
        sampleTarget = 2;
        break;
      case '4':
        sampleTarget = 3;
        break;
      case '=':
        sampleDuration += 50;
        break;
      case '-':
        sampleDuration -= 50;
        break;
    }
  });

  el.addEventListener('click', e => {
    sounds[sampleTarget].setStart(
      (mouseX / WIDTH) * sounds[sampleTarget].getDuration()
    );
  })
