export const trackMouse = () => {
  window.mouseX = 0;
  window.mouseY = 0;

  document.getElementById('canvas').addEventListener('mousemove', e => {
    window.mouseX = e.x - e.target.offsetLeft;
    window.mouseY = e.y - e.target.offsetTop;
  });
}

export const interpolate = (t, n1, n2) => {
  return ((n2 - n1) * t) + n1;
};

export const colourInterpolate = (t, color1, color2) => {
  return {
    r: interpolate(t, color1.r, color2.r),
    g: interpolate(t, color1.g, color2.g),
    b: interpolate(t, color1.b, color2.b),
  }
}

export const randomBetween = (min, max) => {
  return ((max - min) * Math.random()) + min;
}

export const distance = (x1, y1, x2, y2) => {
  let a = x1 - x2;
  let b = y2 - y2;

  return Math.sqrt(a * a + b * b);
}
