function createSample(buffer, context, _start = 0) {
  let sourceNode = null;
  let playing = false;
  let start = _start;

  let startedAt = 0;
  let timeUpdate = () => {};

  const setStart = _start => start = _start;
  let inter;

  const play = (duration = 1) => {
    sourceNode = context.createBufferSource();
    sourceNode.connect(context.destination);
    sourceNode.buffer = buffer;
    sourceNode.start(0, start, duration);

    startedAt = context.currentTime;

    playing = true;

    inter = setInterval(() => {
      update();
    }, 100);
  }

  const stop = () => {
    clearInterval(inter);

    if (sourceNode) {
      sourceNode.disconnect();
      sourceNode.stop(0);
      sourceNode = null;
    }

    playing = false;
  }

  const onTime = (fn = () => {}) => {
    timeUpdate = fn;
  }

  const getDuration = () => {
    return buffer.duration;
  }

  const getStart = () => {
    return {
      start,
      startPerc: start / getDuration()
    };
  }

  const update = () => {
    let curr = start + (context.currentTime - startedAt);
    timeUpdate(curr, curr / getDuration());
  }

  return {
    play,
    stop,
    setStart,
    getDuration,
    onTime,
    getStart
  }
}

export default createSample;
