import Loop from 'raf-loop';

const DEFAULTS = {
    debug: false,
    width: 480,
    height: 480,
    pixelRatio: window.devicePixelRatio,
    clickToggleDebug: true,
    pixelate: 1
};

class SketchEngine {
    constructor(canvasEl, opts = {}) {
        this.options = {
            ...DEFAULTS,
            ...opts
        };

        this.canvasEl = canvasEl;
        this.canvasCtx = this.canvasEl.getContext('2d');
        window.ctx = this.canvasCtx;

        this.frame = 0;

        this.loop = Loop(this.update.bind(this));

        this._renderer = () => {};

        this._addEventListeners();
        this.updateDimensions(this.options.width, this.options.height, this.options.pixelate, this.options.pixelRatio);
    }

    _addEventListeners() {
        this.canvasEl.addEventListener('click', () => {
            this.options.debug = !this.options.debug;

            document.body.classList.toggle('debug', this.options.debug);
        }, false);
    }

    updateDimensions(width, height, pixelate, pixelRatio) {
        this.canvasEl.width = width * pixelRatio;
        this.canvasEl.height = height * pixelRatio;
        this.canvasEl.width = width * pixelRatio;
        this.canvasEl.height = height * pixelRatio;

        this.canvasEl.style.width = `${width * pixelate}px`;
        this.canvasEl.style.height = `${height * pixelate}px`;

        this.canvasCtx.scale(pixelRatio, pixelRatio);
    }

    onRender(fn = () => {}) {
        this._renderer = fn;
    }

    start() {
        this.loop.start();
    }

    update(dt) {
        this._renderer({
            debug: this.options.debug,
            frame: this.frame,
            deltaTime: dt,
            width: this.options.width,
            height: this.options.height
        });

        this.frame++;
    }

    stop() {
        this.loop.stop();
    }
}

export default SketchEngine;
